import articleApi from '@/model/article';
export default {
  name: 'OrderPayDialog',
  data() {
    return {
      isPaid: false,
      isTrans: false,
      payUrl: '',
      dialogTableVisible: false,
      congratulationsVisible: false,
      // 
      form: {
        amount: 0,
        create_time: 'AAA',
        payment_id: 'AAA',
        payment_no: 'AAA',
        status: 'AAA',
        user_id: 0
      },
      activeIndex: '',
      loading: false,
      info: '',
      // 
      remainingTime: 300,
      // 5
      maxTime: 300,
      // 
      countdownTimer: null // 
    };
  },

  computed: {
    // 
    timePercentage() {
      return this.remainingTime / this.maxTime * 100;
    }
  },
  watch: {
    dialogTableVisible(val) {
      if (!val) {
        this.loading = false;
        this.isPaid = false;
        this.isTrans = false;
        // 
        if (this.countdownTimer) {
          clearInterval(this.countdownTimer);
          this.countdownTimer = null;
        }
      }
    }
  },
  methods: {
    show(key, title) {
      this.form = key;
      this.info = title || 'VIP  1';
      this.dialogTableVisible = true;
      this.activeIndex = this.form.payment_no;
      this.loading = false;
      this.startCheckPay(this.form.payment_id); // 
      this.startCountdown(); // 
    },

    closeCongratulations() {
      this.congratulationsVisible = false;
      this.loading = false; // loading
    },

    async CheckPay(payment_id) {
      try {
        // 
        const res = await articleApi.postOrderNoPayCheck2(payment_id);
        if (res === "") {
          this.form.status = res;
          // checkPay
          this.dialogTableVisible = false;
          this.congratulationsVisible = true;
          clearInterval(this.checkPayInterval);
          // 
          if (this.countdownTimer) {
            clearInterval(this.countdownTimer);
            this.countdownTimer = null;
          }
          this.$message.success('');
          //
        }
      } catch (e) {
        console.log(e);
      }
    },
    startCheckPay(payment_id) {
      //  5  CheckPay
      const maxTime = 300 * 1000; // 5 
      let elapsedTime = 0; // 

      //  5  CheckPay
      this.checkPayInterval = setInterval(async () => {
        //  CheckPay 
        await this.CheckPay(payment_id);

        // 
        elapsedTime += 5000;

        //  5 
        if (elapsedTime >= maxTime) {
          clearInterval(this.checkPayInterval);
          console.log('Payment check timeout.');
          //
          this.form.status = '';
          // 
          if (this.countdownTimer) {
            clearInterval(this.countdownTimer);
            this.countdownTimer = null;
          }
        }
      }, 5000); // 5 CheckPay
    },

    //  mm:ss 
    formatTime(seconds) {
      const minutes = Math.floor(seconds / 60);
      const remainingSeconds = seconds % 60;
      return `${minutes.toString().padStart(2, '0')}:${remainingSeconds.toString().padStart(2, '0')}`;
    },
    // 
    startCountdown() {
      // 
      this.remainingTime = this.maxTime;

      // 
      if (this.countdownTimer) {
        clearInterval(this.countdownTimer);
      }

      // 
      this.countdownTimer = setInterval(() => {
        if (this.remainingTime > 0) {
          this.remainingTime -= 1;
        } else {
          // 
          clearInterval(this.countdownTimer);
          this.countdownTimer = null;
        }
      }, 1000);
    }
  },
  // 
  beforeUnmount() {
    if (this.checkPayInterval) {
      clearInterval(this.checkPayInterval);
    }
    if (this.countdownTimer) {
      clearInterval(this.countdownTimer);
    }
  }
};