import "core-js/modules/es.array.push.js";
import Config from '@/config';
import { getToken } from '@/lin/util/token';
import axios from 'axios';
import { marked } from 'marked';
import OrderPayDialog from '@/view/order/order-pay-dialog';
import { ElMessage } from 'element-plus';
export default {
  components: {
    OrderPayDialog
  },
  data() {
    return {
      messages: [{
        role: 'system',
        content: 'AI,,/,,,'
      }],
      inputMessage: '',
      wdMessages: {},
      isLoading: false,
      // 
      typeWriterTimer: null,
      publishDialogVisible: false,
      dialogTitle: '',
      ai_chat_count: this.$store.state.user.ai_chat_count ? this.$store.state.user.ai_chat_count : null
    };
  },
  methods: {
    async sendMessage() {
      if (this.inputMessage.trim() === '') return;
      const str = this.inputMessage;
      this.inputMessage = '';
      this.messages.push({
        role: 'user',
        content: str
      });
      this.scrollToBottom();

      // 
      this.isLoading = true;
      await this.getAiResponse();

      // 
      this.isLoading = false;
      this.scrollToBottom();
    },
    async showPay() {
      this.$router.push('/server');
    },
    async getAiResponse() {
      try {
        const accessToken = getToken('access_token');
        const response = await axios.post(Config.baseURL + `api/blog/articles/chatstream`, this.messages, {
          headers: {
            'Content-Type': 'application/json; charset=utf-8',
            'Authorization': accessToken
          },
          responseType: 'text'
        });
        const lines = response.data.split('\n');
        let fullResponse = '';
        this.messages.push({
          role: 'system',
          content: ''
        });
        for (const line of lines) {
          if (line.trim() === '' || line === 'data: [DONE]') continue;
          const trimmedLine = line.startsWith('data: ') ? line.substring(6) : line;
          try {
            const data = JSON.parse(trimmedLine);
            if (data.choices && data.choices[0] && data.choices[0].delta && data.choices[0].delta.content) {
              const newContent = data.choices[0].delta.content;
              fullResponse += newContent;
            }
          } catch (error) {
            console.error('Error parsing line:', line, error);
          }
        }
        this.typeWriterEffect(fullResponse);
      } catch (error) {
        this.messages.push({
          role: 'system',
          content: ''
        });
        this.inputMessage = '';
      } finally {
        // 
        this.isLoading = false;
      }
    },
    async SendwdMessages() {
      try {
        this.wdMessages.title = this.dialogTitle;
        this.wdMessages.messages = this.messages;
        const accessToken = getToken('access_token');
        const response = await axios.post(Config.baseURL + `api/blog/articles/wdarticle`, this.wdMessages, {
          headers: {
            'Content-Type': 'application/json; charset=utf-8',
            'Authorization': accessToken
          },
          responseType: 'text'
        });
        //truefalse 
        if (response.data === true) {
          ElMessage.success('');
        } else {
          let msg = '<a href="/#/server" style="color: #409EFF; text-decoration: underline;"></a>';
          ElMessage.warning({
            message: msg,
            dangerouslyUseHTMLString: true,
            duration: 6000
          });
        }
        this.publishDialogVisible = false;
      } catch (error) {
        this.messages.push({
          role: 'system',
          content: ''
        });
        ElMessage.error(',');
      } finally {
        // 
        this.isLoading = false;
        this.publishDialogVisible = false;
      }
    },
    typeWriterEffect(content) {
      let index = 0;
      //  role  'system' 
      const lastSystemMessage = this.messages.slice().reverse().find(message => message.role === 'system');

      //  system 
      if (!lastSystemMessage) return;

      // 
      if (this.typeWriterTimer) {
        clearInterval(this.typeWriterTimer);
      }

      // 50ms
      this.typeWriterTimer = setInterval(() => {
        if (index < content.length) {
          lastSystemMessage.content += content[index];
          index++;
          this.scrollToBottom();
        } else {
          clearInterval(this.typeWriterTimer); // 
          this.scrollToBottom();
        }
      }, 20);
      this.scrollToBottom();
    },
    parseMarkdown(content) {
      return marked.parse(content);
    },
    scrollToBottom() {
      setTimeout(() => {
        const chatBody = this.$refs.chatBody;
        chatBody.scrollTop = chatBody.scrollHeight;
      }, 100);
    },
    showPublishDialog() {
      this.dialogTitle = '';
      this.publishDialogVisible = true;
    },
    publishChat() {
      if (!this.dialogTitle.trim()) {
        ElMessage.warning('');
        return;
      }
      const now = new Date();
      const dateStr = `${now.getFullYear()}-${String(now.getMonth() + 1).padStart(2, '0')}-${String(now.getDate()).padStart(2, '0')}`;
      this.dialogTitle = this.dialogTitle + ' ' + dateStr;
      this.SendwdMessages();
    }
  },
  mounted() {
    //  URL 
    const userInput = this.$route.query.user_input;
    const dataId = this.$route.query.data_id;
    if (dataId) {
      //  data_id localStorage
      import('@/lin/util/data-transfer').then(({
        getData,
        removeData
      }) => {
        const data = getData(dataId);
        if (data) {
          // 
          let content = data;

          // 
          this.messages.push({
            role: 'user',
            content: content
          });
          this.isLoading = true;
          this.getAiResponse();
          this.scrollToBottom();

          // localStorage
          removeData(dataId);
        } else {
          ElMessage.warning('');
        }
      }).catch(error => {
        console.error(':', error);
        ElMessage.error('');
      });
    } else if (userInput) {
      //  user_input 
      this.messages.push({
        role: 'user',
        content: userInput
      });
      this.isLoading = true;
      this.getAiResponse();
      this.scrollToBottom();
    }
  },
  computed: {
    loggedIn() {
      return this.$store.state.loggedIn;
    }
  }
};